$fade-purple: #444cf750;

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  height: 100vh;
  overflow: hidden;
}

#root {
  height: 100%;
}

.app {
  height: 100%;
  display: grid;
  grid-template-areas: 'controls-panel controls-panel' 'input-area output-area';
  grid-template-columns: 50% 50%;
  grid-template-rows: auto 1fr;
  background-size: 50px 50px;
  background-image: linear-gradient($fade-purple 1px, transparent 1px),
    linear-gradient(to right, $fade-purple 1px, #ffffff 1px);

  .input-area,
  .output-area {
    textarea {
      height: 190px;
      width: 75%;
      background-color: #ffffff;
    }
  }
}
